import React, { useState } from 'react'
import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'
import NumberChooser from './NumberChooser'
import { sortNumbersAscending } from './utils'

function ChooserModal({ show, setShow, numbers, numberIndex, setNumberInput }) {
  const handleClose = () => {
    setShow(false)
  }

  return (
    <>
      <Modal show={show} onHide={handleClose}>
        <Modal.Body
          style={{
            height: '40vh',
          }}
        >
          <NumberChooser
            minValue={1}
            maxValue={100}
            selectedValue={numbers[numberIndex]}
            onValueChange={(selectedOption) => {
              let newNumbers = [...numbers]
              newNumbers[numberIndex] = selectedOption.value
              // newNumbers = sortNumbersAscending(newNumbers)
              console.log(newNumbers.join(', '))
              setNumberInput(newNumbers.join(', '))
              setShow(false)
            }}
          />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  )
}

export default ChooserModal
