import React, { useEffect, useRef } from 'react'
import Select from 'react-select'

const NumberChooser = ({ minValue, maxValue, selectedValue, onValueChange }) => {
  const generateNumbers = (min, max) => {
    const numbers = []
    for (let i = min; i <= max; i++) {
      numbers.push({ label: `${i}`, value: i })
    }
    return numbers
  }

  const numbers = generateNumbers(minValue, maxValue)
  const selectRef = useRef()

  useEffect(() => {
    if (selectRef.current) {
      selectRef.current.focus()
    }
  }, [])

  return (
    <div style={{ width: '50%', margin: '0 auto' }}>
      <Select
        options={numbers}
        value={numbers.find((number) => number.value === selectedValue)}
        onChange={onValueChange}
        menuIsOpen={true}
        inputRef={selectRef}
      />
    </div>
  )
}

export default NumberChooser
