import React, { Fragment, useState, useMemo } from 'react'
import './index.scss'
import './index.css'

import solvePuzzle, { removeElementsOnce } from './utils'
import NumberCircle from './NumberCircle'
import ChooserModal from './ChooserModal'
import { HashLoader } from 'react-spinners'

const exampleArray = [3, 13, 19, 20, 23, 25]

const parseNumbers = (numbersString) => {
  try {
    return numbersString.split(',').map((n) => Number(n.trim()))
  } catch (e) {
    return []
  }
}

function App() {
  const [numberInput, setNumberInput] = useState(exampleArray.join(', '))
  const [target, setTarget] = useState('456')

  const [showModal, setShowModal] = useState(false)
  const [solution, setSolution] = useState(null)
  const [loading, setLoading] = useState(false)

  const numbers = useMemo(() => {
    return parseNumbers(numberInput)
  }, [numberInput])

  const handleSolve = (e) => {
    e.preventDefault()
    const result = solvePuzzle({
      target: Number(target),
      array: numberInput.split(',').map((n) => Number(n.trim())),
    })
    setSolution(result)
    recreateSolution(result, numberInput)

    setLoading(true)
    window.setTimeout(() => {
      setLoading(false)
    }, 1000)
  }

  const recreateSolution = (solution, numberInput) => {
    const steps = [parseNumbers(numberInput)]
    const { history } = solution
    history.forEach((h, i) => {
      const numbers = h.match(/\d+/g).map((n) => Number(n))
      const newArray = [numbers[2], ...removeElementsOnce(steps[i], [numbers[0], numbers[1]])]
      steps.push(newArray)
    })
    return steps
  }

  return (
    <div className="container py-4 px-5 dark-theme">
      <h1 className="text-center mb-4 text-white">Countdown/Digits Solver</h1>
      <div>
        <form onSubmit={handleSolve}>
          <div className="form-group">
            <label htmlFor="numbers" className="text-white">
              Numbers:
            </label>
            <input
              type="text"
              className="form-control w-50"
              id="numbers"
              value={numberInput}
              onChange={(e) => setNumberInput(e.target.value)}
            />
          </div>
          <div className="form-group mt-2">
            <label htmlFor="target" className="text-white">
              Target:
            </label>
            <input
              type="text"
              className="form-control w-25"
              id="target"
              value={target}
              onChange={(e) => setTarget(e.target.value)}
            />
          </div>
          <button type="submit" className="btn btn-primary btn-large text-white mt-3">
            Solve
          </button>
        </form>
      </div>
      <div className="row mt-5">
        <div className="col text-center">
          {parseNumbers(numberInput).map((number, index) => (
            <Fragment key={index}>
              <NumberCircle
                onClick={() => {
                  console.log('click', index)
                  setShowModal(index)
                }}
                number={number}
                backgroundColor={'#fff'}
                textColor={'#333'}
              />
            </Fragment>
          ))}
          <NumberCircle number={target} backgroundColor={'#800020'} textColor={'#fff'} />
        </div>
      </div>
      <div className="row mt-5">
        {solution && (
          <>
            <div className="col-12 mt-5">
              <div className="bg-secondary p-4 border rounded">
                {loading ? (
                  <div className="d-flex text-center justify-content-center my-4">
                    <HashLoader color="#36d7b7" />
                  </div>
                ) : (
                  <>
                    {solution.solved ? (
                      <>
                        <h2 className="mb-4 text-center text-white">Solution Steps</h2>
                        <div className="row justify-content-center text-center">
                          <div className="col-8 col-sm-6 col-lg-4">
                            <ul className="fs-2 list-group">
                              {solution.history.map((history, index) => (
                                <li key={index} className="list-group-item text-black">
                                  {history}
                                </li>
                              ))}
                            </ul>
                          </div>
                        </div>
                      </>
                    ) : (
                      <h2>No Solution Found</h2>
                    )}
                  </>
                )}
              </div>
            </div>
          </>
        )}
      </div>
      <ChooserModal
        numberIndex={showModal}
        show={showModal !== false}
        setShow={setShowModal}
        numbers={numbers}
        setNumberInput={setNumberInput}
      />
    </div>
  )
}

export default App
